@import "mixins";
@import "fonts";
input[type='number'] {
  -moz-appearance: textfield; }
input[type=number]::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0; }
html {
  overflow-x: hidden;
  width: calc(100% + 8px); }
html, body {
  min-height: 100%; }
body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  margin: 0;
  font-family: 'Helvetica', serif;
  font-weight: normal;
  font-style: normal;
  width: 100%;
  color: #fff;
  overflow: hidden;
  &:active,:hover,:focus {
    outline: 0;
    outline-offset: 0; }
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5; }
  &::-webkit-scrollbar {
    width: 8px;
    background-color: #F5F5F5; }
  &::-webkit-scrollbar-thumb {
    border-radius: 0px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #000; } }
button {
  border: 0;
  border-radius: 0;
  margin: 0;
  font-size: 18px;
  cursor: pointer;
  color: #FFF;
  vertical-align: center;
  @include transition(0.3s ease all); }
h1,h2,h3,h4,h5,p,a,i,span,address,li,label,small {
  font-size: 18px;
  margin: 0;
  line-height: 1.3;
  font-style: normal;
  &::selection {
    background-color: #599255;
    color: #FFF; }
  &::-moz-selection {
    background-color: #599255;
    color: #FFF; } }
h1,h2,h3,h4,h5,p,span,address,li,label,a:not([href]):not([tabindex]) {
  cursor: default; }
a {
  color: #FFF;
  text-decoration: none; }
h2 {
  text-align: center; }
h2, h3 {
  text-transform: uppercase;
  font-size: 36px;
  font-weight: 900; }
ul, li, ol {
  margin: 0;
  padding: 0;
  list-style-type: none; }
.button-mod {
  font-size: 1.2em;
  background-color: #95AF0D;
  padding: 10px 20px;
  border: 1px solid #fff;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
  &:hover {
    background-color: #FFF;
    color: #4A5680;
    border-color: #4A5680; } }
.button-form {
  font-size: 1.2em;
  background-color: #4a5680;
  padding: 10px 20px;
  border: 1px solid #E9E9E9;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
  &:hover {
    background-color: #FFF;
    color: #4a5680;
    border-color: #4a5680; } }
.overfloff {
  overflow: hidden;
  width: 100%; }
.text-left {
  text-align: left!important; }
.text-center {
  text-align: center!important; }
.text-right {
  text-align: right!important; }
.w-100 {
  width: 100%; }
.h-100 {
  height: 100%; }
.db {
  display: block; }
.mx-a {
  margin-right: auto;
  margin-left: auto; }
.political {
  text-align: left;
  position: relative;
  margin-bottom: 30px;
  label {
    display: inline-block;
    padding-left: 30px; }
  label, a {
    color: #4a5680;
    line-height: 1em; }
  [type="checkbox"] {
    position: absolute;
    opacity: 0;
    &+label {
      &:before, &:after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        @include transition(.3s); }
      &:after {
        content: url("../img/icons/politicalYes.svg");
        margin-top: 7px;
        margin-left: 4px;
        will-change: transform;
        transform-origin: left bottom; }
      &:before {
        width: 20px;
        height: 20px;
        border: 2px solid #599255;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        margin-top: 7px;
        background: transparent; } }
    &:checked {
      &+label {
        &:after {
          @include transform(scale(1.1)); } } }
    &:not(:checked) {
      &+label, &+label a {
        color: #F00;
        &:after {
          @include transform(scale(0));
          opacity: 0; }
        &:before {
          border-color: #F00; } } } }
  a {
    text-decoration: #4a5680 solid underline;
    @include transition(.3s);
    &:hover {
      opacity: .7; } } }
